<template>
    <div class="editor-box">
        <div class="page-title">
            <span></span>
            <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <el-form ref="form" :model="form" :rules="rules" label-width="100px" class="page-form" label-position="left">
            <el-form-item label="">
                <el-col :span="4">
                    <el-select v-model="form.cate_1st" placeholder="" class="page-select" @change="getSecondClass">
                        <el-option
                                v-for="item in firstClass"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id">
                        </el-option>
                    </el-select>
                </el-col>
                <el-col :span="4">
                    <el-select v-model="form.cate_2st" placeholder="" class="page-select" @change="getThirdClass">
                        <el-option
                                v-for="item in secondClass"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id">
                        </el-option>
                    </el-select>
                </el-col>
                <el-col :span="4">
                    <el-select v-model="form.cate_3st" placeholder="" class="page-select">
                        <el-option
                                v-for="item in thirdClass"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id">
                        </el-option>
                    </el-select>
                </el-col>
            </el-form-item>
            <el-form-item label="" prop="name">
                <el-row>
                    <el-col :span="10">
                        <el-input v-model="form.name"></el-input>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="16">
                        <span class="page-tips">
                           3200
                        </span>
                    </el-col>
                </el-row>
            </el-form-item>
            <el-form-item label="" prop="sellingPoint">
                <el-row>
                    <el-col :span="10">
                        <el-input type="textarea" v-model="form.hotPoint"></el-input>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="16">
                        <span class="page-tips">
                           140
                        </span>
                    </el-col>
                </el-row>
            </el-form-item>
            <el-form-item label="" prop="goodPrice">
                <el-row>
                    <el-col :span="3">
                        <el-input placeholder="" v-model="form.price">
                            <template slot="append"></template>
                        </el-input>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="16">
                        <span class="page-tips">
                          0-9999999
                        </span>
                        <span class="page-tips">
                          
                        </span>
                    </el-col>
                </el-row>
            </el-form-item>
            <el-form-item label="">
                <el-row>
                    <el-col :span="3">
                        <el-input placeholder="" v-model="form.marketPrice">
                            <template slot="append"></template>
                        </el-input>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="16">
                        <span class="page-tips">
                            0-9999999
                        </span>
                    </el-col>
                </el-row>
            </el-form-item>
            <el-form-item label="">
                <el-row>
                    <el-col :span="3">
                        <el-input placeholder="" v-model="form.cost">
                            <template slot="append"></template>
                        </el-input>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="16">
                        <span class="page-tips">
                             0-9999999
                        </span>
                    </el-col>
                </el-row>
            </el-form-item>
            <el-form-item label="">
                <el-row>
                    <el-col :span="3">
                        <el-input disabled v-model="form.discount">
                            <template slot="append"></template>
                        </el-input>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="16">
                        <span class="page-tips">
                            
                        </span>
                    </el-col>
                </el-row>
            </el-form-item>
            <el-form-item label="">
                <el-row>
                    <el-col :span="3">
                        <el-input v-model="form.inventory">
                            <template slot="append"></template>
                        </el-input>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="16">
                        <span class="page-tips">
                            0-9999999
                        </span>
                        <span class="page-tips">
                         
                        </span>
                    </el-col>
                </el-row>
            </el-form-item>
            <el-form-item label="">
                <el-row>
                    <el-col :span="3">
                        <el-input v-model="form.articleNo">
                        </el-input>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="16">
                        <span class="page-tips">
                            
                        </span>
                        <span class="page-tips">
                            20
                        </span>
                    </el-col>
                </el-row>
            </el-form-item>
            <el-form-item label="">
                <el-row>
                    <el-upload
                            class="avatar-uploader"
                            action="/api/upload/goods"
                            :show-file-list="false"
                            :headers="headers"
                            :on-success="handleAvatarSuccess"
                            :before-upload="beforeAvatarUpload">
                        <img v-if="form.img_lg" :src="form.img_lg" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </el-row>
                <el-row>
                    <el-col :span="16">
                        <span class="page-tips">
                            jpgifpng
                            800*8001M
                        </span>
                    </el-col>
                </el-row>
            </el-form-item>
            <el-form-item label="">
                <el-row>
                    <el-upload
                            action="/api/upload/slider"
                            list-type="picture-card"
                            :headers="headers"
                            :on-success="sliderSucceed"
                            :before-upload="sliderUpload"
                            :on-remove="handleRemove">
                        <i class="el-icon-plus"></i>
                    </el-upload>
                    <el-dialog :visible.sync="dialogVisible">
                        <img width="100%" :src="form.slider" alt="">
                    </el-dialog>
                </el-row>
                <el-row>
                    <el-col :span="16">
                        <span class="page-tips">
                                jpgifpng
                            800*8001M
                        </span>
                    </el-col>
                </el-row>
            </el-form-item>
            <el-alert
                    title=""
                    type="info"
                    effect="dark"
                    class="alert-box"
                    :closable="false">
            </el-alert>
            <el-form-item label="">
                <el-col :span="10">
                    <el-input v-model="form.brand"></el-input>
                </el-col>
            </el-form-item>
            <el-form-item label="">
                <el-col :span="22">
                    <Editor></Editor>
                </el-col>
            </el-form-item>
            <el-alert
                    title=''
                    type="info"
                    effect="dark"
                    class="alert-box"
                    :closable="false">
            </el-alert>
            <!--            <el-form-item label="">-->
            <!--                <el-col :span="4">-->
            <!--                    <el-select  placeholder="" class="page-select">-->
            <!--                        <el-option label="" value="shanghai"></el-option>-->
            <!--                        <el-option label="" value="beijing"></el-option>-->
            <!--                    </el-select>-->
            <!--                </el-col>-->
            <!--                <el-col :span="4">-->
            <!--                    <el-select placeholder="" class="page-select">-->
            <!--                        <el-option label="" value="shanghai"></el-option>-->
            <!--                        <el-option label="" value="beijing"></el-option>-->
            <!--                    </el-select>-->
            <!--                </el-col>-->
            <!--                <el-col :span="4">-->
            <!--                    <el-select placeholder="" class="page-select">-->
            <!--                        <el-option label="" value="shanghai"></el-option>-->
            <!--                        <el-option label="" value="beijing"></el-option>-->
            <!--                    </el-select>-->
            <!--                </el-col>-->
            <!--            </el-form-item>-->
            <el-form-item label="">
                <el-row>
                    <el-col :span="3">
                        <el-input placeholder="" v-model="form.freight">
                            <template slot="append"></template>
                        </el-input>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="16">
                        <span class="page-tips">
                            0
                        </span>
                    </el-col>
                </el-row>
            </el-form-item>
            <el-form-item>
                <el-button type="success" round @click="submit"></el-button>
            </el-form-item>
        </el-form>

    </div>
</template>

<script>
    import {mapState} from 'vuex';
    import Editor from '@/views/goods/Editor.vue';

    export default {
        name: "Release",
        components: {
            Editor,
        },
        data() {
            return {
                form: {
                    name: '',
                    hotPoint: '',
                    price: '',
                    marketPrice: '',
                    cost: '',
                    discount: 0,
                    inventory: '',
                    articleNo: '',
                    img_lg: '',
                    img_md: '',
                    slider: '',
                    brand: '',
                    detail: '',
                    freight: '',
                    cate_1st: '',
                    cate_2nd: '',
                    cate_3rd: '',
                },
                sliderArr: [],
                rules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'},
                        {min: 3, max: 200, message: ' 3  200 ', trigger: 'blur'}
                    ],
                    hotPoint: [
                        {required: true, min: 1, max: 140, message: ' 1  140 ', trigger: 'blur'}
                    ],
                    goodPrice: [],
                },
                dialogVisible: false,
                firstClass: {},
                secondClass: {},
                thirdClass: {},
                headers: {
                    Authorization: `Bearer ${sessionStorage.token}`,
                },


            };
        },
        methods: {
            handleAvatarSuccess(res, file) {
                this.form.img_lg = res.lgImg;
                this.form.img_md = res.mdImg;
            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg' || 'image/png';
                const isLt2M = file.size / 1024 / 1024 < 2;

                if (!isJPG) {
                    this.$message.error(' JPG /PNG!');
                }
                if (!isLt2M) {
                    this.$message.error(' 2MB!');
                }
                return isJPG && isLt2M;
            },
            //

            handleRemove(file, fileList) {
                let arr = [];
                fileList.forEach(function (item) {
                    arr.push(item.response.src)
                })
                this.sliderArr = [...arr];
                this.form.slider = this.sliderArr.join(',');
                console.log(this.form.slider)
            },
            sliderUpload(file) {
                const isJPG = file.type === 'image/jpeg' || 'image/png';
                const isLt2M = file.size / 1024 / 1024 < 2;

                if (!isJPG) {
                    this.$message.error(' JPG /PNG!');
                }
                if (!isLt2M) {
                    this.$message.error(' 2MB!');
                }
                return isJPG && isLt2M;
            },
            sliderSucceed(res) {
                this.sliderArr.push(res.src);
                this.form.slider = this.sliderArr.join(',');

            },
            //
            getFirstClass() {
                this.$http.get('/api/category/sub', {
                    params: {
                        pId: 1,
                    },
                }).then((res) => {
                    this.firstClass = res.data;
                    console.log(this.firstClass)
                });
            },
            getSecondClass() {
                this.secondClass = {};
                this.form.cate_2st = '';
                this.thirdClass = {};
                this.form.cate_3rd = '';

                this.$http.get('/api/category/sub', {
                    params: {
                        pId: this.form.cate_1st,
                    },
                }).then((res) => {
                    this.secondClass = res.data;

                });
            },
            getThirdClass() {
                this.thirdClass = {};
                this.form.cate_3rd = '';
                this.$http.get('/api/category/sub', {
                    params: {
                        pId: this.form.cate_2st,
                    },
                }).then((res) => {
                    this.thirdClass = res.data;

                });
            },
            //
            submit() {
                if (this.form.cate_1st == '') {
                    this.form.cate_1st = 0;
                }
                if (this.form.cate_2nd == '') {
                    this.form.cate_2nd = 0;
                }
                if (this.form.cate_3rd == '') {
                    this.form.cate_3rd = 0;
                }
                this.form.detail = this.editorContent;
                console.log({...this.form});
                this.$http.post('/api/goods/release', {
                    ...this.form
                }).then((res) => {
                    this.$message(res.msg);
                    this.form = {
                        name: '',
                        hotPoint: '',
                        price: '',
                        marketPrice: '',
                        cost: '',
                        discount: 0,
                        inventory: '',
                        articleNo: '',
                        img_lg: '',
                        img_md: '',
                        slider: '',
                        brand: '',
                        detail: '',
                        freight: '',
                        cate_1st: '',
                        cate_2nd: '',
                        cate_3rd: '',
                    };
                })
            },
        },
        created() {
            this.getFirstClass();
        },
        computed: mapState({
            editorContent: state => state.release.editorContent,
        })
    }
</script>

<style lang="scss">
    .editor-box {
        .page-title {
            display: flex;
            justify-content: space-between;
            padding-bottom: 20px;
            border-bottom: 1px solid #999999;

            > span {
                font-size: 20px;
                font-weight: bolder;
            }
        }

        .page-form {
            margin-top: 20px;
        }

        .page-select {
            width: 100%;
        }

        .avatar-uploader {
            .el-upload {
                border: 1px dashed #d9d9d9;
                border-radius: 6px;
                cursor: pointer;
                position: relative;
                overflow: hidden;

                &:hover {
                    border-color: #409EFF;
                }
            }

            .avatar-uploader-icon {
                font-size: 28px;
                color: #8c939d;
                width: 150px;
                height: 150px;
                line-height: 150px;
                text-align: center;
            }

            .avatar {
                width: 178px;
                height: 178px;
                display: block;
            }
        }


        .alert-box {
            margin-bottom: 20px;

            .el-alert__title {
                font-size: 16px;
            }
        }

        .page-tips {
            display: block;
            color: #A6A7A9;
        }
    }
</style>